import { styleHeaderBanner } from "./Elements/HeaderConstant";
import { TagboxUpload } from '../../../constants' 
import './scss/HeaderType4.scss'
import { ensureFullyQualifiedUrl } from "../../../utils/Common";

const HeaderType4 = ({ Banner }) => {
  const { bannerStyle, subTitleStyle, titleStyle, iconAreaStyle, iconStyle, isFacebook, isTwitter, isInstagram, facebookIconAreaStyle, facebookIconStyle, twitterIconAreaStyle, twitterIconStyle, instagramIconAreaStyle, instagramIconStyle, subTitleVarentClass, titleVarentClass } = styleHeaderBanner(Banner);

  var bCustomStyle = bannerStyle;
  
  const NetworkIcon = (url,iconStyle,networkStyle,iconClass,label,iconAreaStyle,iconAreaStyleNetwork)=> <a href={url ? ensureFullyQualifiedUrl(url) : '#'} target="blank" className="tb_header4_social_icon_list__" role="button" aria-label={label} rel="nofollow" style={{ ...iconAreaStyle, ...iconAreaStyleNetwork }}>
    <div className={`tb_header4_social_icon__ tb__icon  ${iconClass}`} style={{ ...iconStyle, ...networkStyle }}> </div>
  </a> 
 
  if (bCustomStyle && bCustomStyle.backgroundImage.includes("bg-bnr-3.png") && bCustomStyle.backgroundImage.includes(TagboxUpload)) bCustomStyle.backgroundImage = bCustomStyle.backgroundImage.replace(TagboxUpload, "https://test.taggbox.com/widget/assets/media/banner/")
  return <div className="tb_wall_header_wrap__" style={bCustomStyle}>
    <div className="tb_header4_row__">
      <div className="tb_header4_column_content__">
        <div className="tb_header4_content__">
          <div className={`tb_header4_subtitle__ tb-cTBfont-${subTitleVarentClass}`} style={subTitleStyle}>{Banner.subTitle}</div>
          <div className={`tb_header4_title__ tb-cTBfont-${titleVarentClass}`} style={titleStyle}>{Banner.title}</div>
        </div>
      </div>
      <div className="tb_header4_column_icons__">
        {Banner.social_icon_status == 1 ? <div className="tb_header4_social_icons__">

          {isFacebook ?
            <NetworkIcon url={Banner.facebook_url} iconStyle={iconStyle} networkStyle={facebookIconStyle} iconAreaStyle={iconAreaStyle} iconAreaStyleNetwork={facebookIconAreaStyle} iconClass="tb-facebook" label="Facebook"/>
             : ''}
            {isTwitter ?
            <NetworkIcon url={Banner.twitter_url} iconStyle={iconStyle} networkStyle={twitterIconStyle} iconAreaStyle={iconAreaStyle} iconAreaStyleNetwork={twitterIconAreaStyle}  iconClass="tb-twitter" label="Twitter"/>
           : ''}
          {isInstagram ?
            <NetworkIcon url={Banner.instagram_url} iconStyle={iconStyle} networkStyle={instagramIconStyle} iconAreaStyle={iconAreaStyle} iconAreaStyleNetwork={instagramIconAreaStyle}  iconClass="tb-instagram" label="Instagram"/>
             : ''}
        
        </div>
          : null}</div>
    </div>
  </div>
}
export default HeaderType4
